<template>
  <div class="column is-8 is-offset-2">

          <!-- START ARTICLE -->
          <div class="card article" v-for="list in data">
            <div class="card-content">
              <div class="media">
                <div class="media-center">
                  <img src="http://www.radfaces.com/images/avatars/daria-morgendorffer.jpg" class="author-image" alt="Placeholder image">
                </div>
                <div class="media-content has-text-centered">
                  <p class="title article-title">{{list.title.rendered}}</p>
                  <p class="subtitle is-6 article-subtitle">
                    <span>{{list.date}}</span>
                    <!-- <a href="#">@d</a> on October 7, 2017 -->
                  </p>
                </div>
              </div>

              <div v-html="list.excerpt.rendered" class="content article-body">
                <!-- {{list.excerpt.rendered}} -->
              </div>
            </div>
          </div>
          <!-- END ARTICLE -->

  </div>
</template>
<script>
import axios from 'axios';

export default {
  data() {
    return {
      data: []
    };
  },
  created() {
    axios.get("http://thesuperblack.hol.es/wp-json/wp/v2/posts")
      .then(response => {
        // console.log('respon api succ')
        return (this.data = response.data);
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>

<style>

</style>



